.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar-logo h1 {
  font-size: 28px;
  font-weight: bold;
  color: #2c3e50;
}

.navbar-logo span {
  color: #3498db;
}

.navbar-links {
  display: flex;
  gap: 20px;
  list-style: none;
}

.navbar-links li {
  cursor: pointer;
  font-size: 16px;
  color: #555;
}

.navbar-actions .login-btn,
.navbar-actions .lawyer-btn {
  margin-left: 10px;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.login-btn {
  background-color: transparent;
  color: #3498db;
  border: 1px solid #3498db;
}

.lawyer-btn {
  background-color: #3498db;
  color: white;
}
