/* src/components/Offerings.css */
.offerings {
  text-align: center;
  padding: 50px 0;
  background-color: #f9f9f9;
}

.offerings h2 {
  font-size: 32px;
  color: #1e60d3;
  margin-bottom: 30px;
}

.offerings-grid {
  display: flex;
  justify-content: center;
  gap: 20px; /* Space between offering items */
  flex-wrap: wrap; /* Ensure items wrap on smaller screens */
}

.offering {
  background-color: white;
  width: 250px; /* Fixed width for each offering */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease;
}

.offering:hover {
  transform: scale(1.05); /* Slight hover effect */
}

.offering img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 5px;
}

.offering p {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  margin-top: 10px;
}

/* Ensure responsiveness on smaller screens */
@media (max-width: 768px) {
  .offerings-grid {
    justify-content: center;
  }

  .offering {
    width: 100%; /* Full width on smaller screens */
    margin-bottom: 20px; /* Add margin between items */
  }
}
